.App {
  margin: 3rem auto;
  text-align: center;
  width: 37vw;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#3b679e),color-stop(40%, #2b88d9),color-stop(43%, #2b88d9),color-stop(49%, #207cca),color-stop(56%, #207cca),to(#7db9e8));
  background-image: -webkit-linear-gradient(top, #3b679e 0%,#2b88d9 40%,#2b88d9 43%,#207cca 49%,#207cca 56%,#7db9e8 100%);
  background-image: -o-linear-gradient(top, #3b679e 0%,#2b88d9 40%,#2b88d9 43%,#207cca 49%,#207cca 56%,#7db9e8 100%);
  background-image: linear-gradient(to bottom, #3b679e 0%,#2b88d9 40%,#2b88d9 43%,#207cca 49%,#207cca 56%,#7db9e8 100%);
  border-radius: 5px;
  -webkit-box-shadow: 0 6px 28px -3px rgba(0,0,0,0.75);
          box-shadow: 0 6px 28px -3px rgba(0,0,0,0.75);
  color: white;
  position: relative;

}

.form {
  position: absolute;
  left: 5rem;
  font-size: 1rem;
}

.forecastWrapper {
  margin-top: 5rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
}

@media (max-width: 900px) {

  .App {
    width: 100vw;
    margin: 0 auto;
  }
}






