

.dayTwo {
    display: inline-block;
    margin: 0 auto;
    -webkit-transition: all .2s ;
    -o-transition: all .2s ;
    transition: all .2s ;
    border-bottom: 2px solid rgba(255, 255, 255, .5);





}

.dayTwo:hover {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
}

.dayTwo> img {
    width: 5.5rem;
}


