.dayThree {
    display: inline-block;
    -webkit-transition: all .2s ;
    -o-transition: all .2s ;
    transition: all .2s ;
    margin: 0 auto;
    border-bottom: 2px solid rgba(255, 255, 255, .5);



}

.dayThree:hover {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
}

.dayThree> img {
    width: 5.5rem;
}


